.widgets {
  position: sticky;
  top: 70px;
  flex: 0.2;
  background-color: white;
  border-radius: 10px;
  border: 1px solid lightgray;
  padding-bottom: 10px;
  height: fit-content;
}
.widgets_header {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-top: 5px;
}
.widgets_header > h2 {
  font-size: 1rem;
  font-weight: bold;
}
.widgets_article {
  display: flex;
  margin-top: 15px;
  justify-content: space-between;
}
.widgets_article:hover {
  background-color: whitesmoke;
  cursor: pointer;
}
.widgets_articleLeft {
  color: #2e78b8;
  margin-right: 5px;
  margin-top: 2px;
}
.widgets_articleLeft > .MuiSvgIcon-root {
  font-size: 16px;
}
.widgets_articleRight > h2 {
  font-size: 16px;
}
.widgets_articleRight > p {
  font-size: 14px;
}
