.sidebar {
  position: sticky;
  top: 70px;
  flex: 0.2;
  text-align: center;
  height: fit-content;
}
.sidebar_top {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid lightgray;
  border-bottom: 0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: white;
  padding-bottom: 10px;
}
.sidebar_top > h2 {
  font-size: 18px;
}
.sidebar_top > h4 {
  color: grey;
  font-size: 12px;
}
.sidebar_top > img {
  width: 100%;
  height: 60px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  margin-bottom: -20px;
  object-fit: cover;
}
.sidebar_avatar {
  margin-bottom: 10px;
}
.sidebar_stats {
  background-color: white;
  border: 1px solid lightgray;

  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  padding: 10px;
  margin-bottom: 10px;
}
.sidebar_stat {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
.sidebar_stat {
  color: grey;
  font-size: 13px;
  font-weight: 600;
}
.sidebar_statNumber {
  font-weight: bold;
  color: #2e78b8;
}
.sidebar_bottom {
  margin-top: 10px;
  padding: 10px;
  text-align: start;
  background-color: white;
  border: 1px solid lightgray;
  border-radius: 10px;
}
.sidebar_bottom > p {
  /* margin-top: 10px;
   */
  font-size: 13px;
  padding-bottom: 10px;
  text-transform: capitalize;
}
.sidebar_recentitem {
  display: flex;
  font-size: 13px;
  font-weight: bold;
  color: grey;
  cursor: pointer;
  padding: 5px;
  margin-bottom: 5px;
}
.hash_tag {
  margin-right: 10px;
  margin-left: 5px;
}
.sidebar_recentitem:hover {
  background-color: whitesmoke;
  color: black;
  border-radius: 10px;
}
