.post {
  background-color: white;
  padding: 15px;
  border-radius: 10px;
  margin-bottom: 10px;
}
.post_header {
  display: flex;
  margin-bottom: 10px;
  align-items: center;
}
.post_info {
  margin-left: 10px;
}
.post_info > h2 {
  font-size: 15px;
}
.post_info > p {
  font-size: 12px;
  color: grey;
}
.post_body {
  overflow-wrap: break-word;
  margin-left: 20px;
}
.post_button {
  display: flex;
}
